/**
 * Tests for useEntranceAnimation hook
 * 
 * Covers:
 * - Different entrance animation types (fadeIn, slideIn, scaleIn, bounceIn)
 * - Delay handling
 * - Animation style generation
 * - Start/restart functionality
 */

import { renderHook, waitFor } from '@testing-library/react-native';
import { useEntranceAnimation } from '../useEntranceAnimation';

describe('useEntranceAnimation', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  describe('fadeIn animation', () => {
    it('should initialize fadeIn animation correctly', () => {
      const { result } = renderHook(() => useEntranceAnimation('fadeIn'));

      expect(result.current.entranceStyle).toBeDefined();
      expect(result.current.start).toBeDefined();
    });

    it('should handle fadeIn with no delay', () => {
      const { result } = renderHook(() =>
        useEntranceAnimation('fadeIn', 0)
      );

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should handle fadeIn with delay', () => {
      const { result } = renderHook(() =>
        useEntranceAnimation('fadeIn', 300)
      );

      expect(result.current.entranceStyle).toBeDefined();
    });
  });

  describe('slideIn animation', () => {
    it('should initialize slideIn animation correctly', () => {
      const { result } = renderHook(() => useEntranceAnimation('slideIn'));

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should handle slideIn with delay', () => {
      const { result } = renderHook(() =>
        useEntranceAnimation('slideIn', 200)
      );

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should provide start function for slideIn', () => {
      const { result } = renderHook(() => useEntranceAnimation('slideIn'));

      expect(typeof result.current.start).toBe('function');
      result.current.start();
    });
  });

  describe('scaleIn animation', () => {
    it('should initialize scaleIn animation correctly', () => {
      const { result } = renderHook(() => useEntranceAnimation('scaleIn'));

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should handle scaleIn with delay', () => {
      const { result } = renderHook(() =>
        useEntranceAnimation('scaleIn', 400)
      );

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should restart scaleIn animation', () => {
      const { result } = renderHook(() => useEntranceAnimation('scaleIn'));

      result.current.start();
    });
  });

  describe('bounceIn animation', () => {
    it('should initialize bounceIn animation correctly', () => {
      const { result } = renderHook(() => useEntranceAnimation('bounceIn'));

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should handle bounceIn with delay', () => {
      const { result } = renderHook(() =>
        useEntranceAnimation('bounceIn', 500)
      );

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should restart bounceIn animation', () => {
      const { result } = renderHook(() => useEntranceAnimation('bounceIn'));

      result.current.start();
    });
  });

  describe('animatedStyle compatibility', () => {
    it('should provide both entranceStyle and animatedStyle', () => {
      const { result } = renderHook(() => useEntranceAnimation('fadeIn'));

      expect(result.current.entranceStyle).toBeDefined();
      expect(result.current.animatedStyle).toBeDefined();
      expect(result.current.entranceStyle).toBe(result.current.animatedStyle);
    });
  });

  describe('start function', () => {
    it('should restart fadeIn animation when called', () => {
      const { result } = renderHook(() => useEntranceAnimation('fadeIn'));

      result.current.start();
      expect(result.current.start).toBeDefined();
    });

    it('should restart slideIn animation when called', () => {
      const { result } = renderHook(() => useEntranceAnimation('slideIn'));

      result.current.start();
      expect(result.current.start).toBeDefined();
    });

    it('should restart scaleIn animation when called', () => {
      const { result } = renderHook(() => useEntranceAnimation('scaleIn'));

      result.current.start();
      expect(result.current.start).toBeDefined();
    });

    it('should restart bounceIn animation when called', () => {
      const { result } = renderHook(() => useEntranceAnimation('bounceIn'));

      result.current.start();
      expect(result.current.start).toBeDefined();
    });
  });

  describe('rerender behavior', () => {
    it('should maintain consistent API on rerender', () => {
      const { result, rerender } = renderHook(
        (props) => useEntranceAnimation(props.type, props.delay),
        { initialProps: { type: 'fadeIn' as const, delay: 0 } }
      );

      const firstStyle = result.current.entranceStyle;
      const firstStart = result.current.start;

      rerender({ type: 'fadeIn' as const, delay: 0 });

      expect(result.current.entranceStyle).toBeDefined();
      expect(result.current.start).toBeDefined();
    });

    it('should handle animation type change', () => {
      const { result, rerender } = renderHook(
        (props) => useEntranceAnimation(props.type),
        { initialProps: { type: 'fadeIn' as const } }
      );

      expect(result.current.entranceStyle).toBeDefined();

      rerender({ type: 'slideIn' as const });

      expect(result.current.entranceStyle).toBeDefined();
    });

    it('should handle delay change', () => {
      const { result, rerender } = renderHook(
        (props) => useEntranceAnimation('fadeIn', props.delay),
        { initialProps: { delay: 0 } }
      );

      expect(result.current.entranceStyle).toBeDefined();

      rerender({ delay: 300 });

      expect(result.current.entranceStyle).toBeDefined();
    });
  });
});

